ol {
  margin: 0 25px;
}

.difficulty-ratings {
  list-style: none;
  text-align: center;
  padding: 0;
}
.difficulty-ratings li {
  display: inline;
  margin: 40px;
  color: #fff;
}

.stitch-display .card-title {
  font-size: 16px;
}

.stitch-display img {
  border-radius: 5px;
  max-height: 250px;
}

.pattern-display {
  margin-top: 150px;
  padding: 20px;
}
.pattern-display h4 {
  text-align: center;
}
.pattern-display p {
  text-align: justify;
}
.stitch-title {
  text-align: center;
  color: #3a0ca3;
  font-weight: bold;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 25px;
  padding: 15px;
  font-size: 25px;
  margin: 25px auto;
  max-width: 80%;
}
.row {
  margin: 0 auto;
}
.yarn-calculator {
  text-align: center;
  margin: 50px auto;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 25px;
  padding: 15px;
  font-size: 25px;
  max-width: 80%;
}

/*CSS media queries*/
@media only screen and (max-width: 600px) {
  .difficulty-ratings li {
    display: block;
  }
}
